import {
    Component, OnInit, Inject, ViewChild, Output,
    EventEmitter
} from '@angular/core';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import * as moment from 'moment';
import { IAppConfig } from '../../../../interfaces/iapp-config';
import { InsuranceListComponent } from '../insurance/insurance-list.component';
import { BaseComponent } from '../../../../base.component';
import { LaborContractListComponent } from '../labor-contract/labor-contract-list.component';
import { AcademicLevelComponent } from '../academic-level/academic-level.component';
import { EmploymentHistoryListComponent } from '../employment_history/employment-list.component';
import { TrainingHistoryListComponent } from '../training-history/training-history-list.component';
import { CommendationDisciplineListComponent } from '../commendation-discipline/commendation-discipline-list.component';
import { RecordsManagementFormComponent } from '../records-management/records-management-form.component';
import { DestroySubscribers } from '../../../../shareds/decorator/destroy-subscribes.decorator';
import { User } from '../models/user.model';
import { APP_CONFIG } from '../../../../configs/app.config';
import { IPageId, PAGE_ID } from '../../../../configs/page-id.config';
import { AppService } from '../../../../shareds/services/app.service';
import { UserDynamicHostDirective } from '../user-dynamic-host.directive';
import { HelperService } from '../../../../shareds/services/helper.service';
import { UserService } from '../services/user.service';
import { finalize } from 'rxjs/operators';

@Component({
    selector: 'app-user-detail',
    styleUrls: ['../../../../../assets/pages/css/profile.css'],
    templateUrl: './user-detail.component.html',
    providers: [
        HelperService,
        Location, {provide: LocationStrategy, useClass: PathLocationStrategy}
    ]
})

@DestroySubscribers()
export class UserDetailComponent extends BaseComponent implements OnInit {
    @ViewChild(UserDynamicHostDirective) userDynamicHost: UserDynamicHostDirective;
    @Output() onCloseForm = new EventEmitter();
    userInfo: User = new User();
    formValue = 1;

    constructor(@Inject(APP_CONFIG) public appConfig: IAppConfig,
                @Inject(PAGE_ID) public pageId: IPageId,
                private location: Location,
                private route: ActivatedRoute,
                private router: Router,
                private title: Title,
                private appService: AppService,
                private userService: UserService,
                private helperService: HelperService) {
        super();
    }

    ngOnInit() {
        this.subscribers.routeData = this.route.data.subscribe((data: { detail: any }) => {
            const detail = data.detail;
            if (detail) {
                this.userInfo = detail;
                this.pageTitle = `Thông tin người dùng: ${detail.fullName}`;
                this.title.setTitle(this.pageTitle);
                this.appService.setupPage(this.pageId.HR, this.pageId.USER, 'Quản lý người dùng', this.pageTitle);
            }
        });
    }

    getDetail(userId: string) {
        this.isSearching = true;
        this.userService.getDetail(userId)
            .pipe(finalize(() => this.isSearching = false))
            .subscribe(result => {
                // this.userInfo = result;
            });
    }

    print() {
        const style = `* {
                                margin: 0;
                                padding: 0;
                                font-size: 12px;
                                box-sizing: border-box;
                            }

                            @page {
                                size: auto;
                                /* auto is the initial value */
                                margin: 0mm;
                                /* this affects the margin in the printer settings */
                            }

                            html {
                                background-color: #FFFFFF;
                                margin: 0px;
                                /* this affects the margin on the html before sending to printer */
                            }

                            body {
                                margin: 5mm 10mm 5mm 10mm;
                            }

                            table div {
                                padding: 2px 3px;
                                width: 100%;
                                border: 1px solid #999;
                                min-height: 25px; width: 100%;
                            }

                            .red {
                                color: red;
                            }

                            .user-info-print-container {
                                position: relative;
                            }

                            .user-info-print-container .header {
                                text-align: center;
                            }

                            .user-info-print-container .header img {
                                width: 75%;
                            }

                            .user-info-print-container .footer {
                                position: fixed;
                                bottom: 0;
                                left: 0;
                                right: 0;
                                margin: 0 auto;
                            }

                            .user-info-print-container .footer img {
                                width: 100%;
                            }

                            .user-info-print-container .title {
                                font-size: 20px;
                                font-weight: bold;
                                text-align: center;
                                margin-top: 15px;
                                margin-bottom: 20px;
                            }

                            .user-info-print-container .avatar-container {
                                width: 20%;
                                float: left;
                                text-align: center;
                            }

                            .user-info-print-container .avatar-container img {
                                width: 100%;
                            }

                            .user-info-print-container .avatar-container .full-name {
                                font-weight: bold;
                                margin-top: 15px;
                                text-transform: uppercase;
                            }

                            .user-info-print-container .avatar-container img{
                                border-radius: 50%;
                            }

                            .user-info-print-container .info-container {
                                width: 80%;
                                float: left;
                            }

                            .user-info-print-container .info-container table {
                                width: 100%;
                            }

                            .user-info-print-container .info-container table tr td {
                                padding: 4px 10px;
                            }
                            .user-info-print-container .info-container table tr td.w20 {
                                width:100px;
                            }
                            .user-info-print-container .info-container table tr td.w30 {
                                width:30%;
                            }`;
        const content = `
                        <!-- END: .container -->`;
        this.helperService.openPrintWindow('Thông tin nhân viên', content, style);
    }

    loadComponent(value) {
        this.formValue = value;
        const viewContainerRef = this.userDynamicHost.viewContainerRef;
        viewContainerRef.clear();
        switch (value) {
            // Load component labor-contract
            case 2:
                const laborInstance = this.helperService.createComponent(viewContainerRef, LaborContractListComponent);
                laborInstance.userId = this.userInfo.id;
                laborInstance.getAllContractTypes();
                laborInstance.search(1);
                break;
            // Load component academic-level
            case 3:
                const academicInstance = this.helperService.createComponent(viewContainerRef, AcademicLevelComponent);
                academicInstance.userId = this.userInfo.id;
                academicInstance.allowAdd = false;
                academicInstance.search(1);
                break;
            // Load component insuarance-history
            case 4:
                const insuaranceInstance = this.helperService.createComponent(viewContainerRef, InsuranceListComponent);
                insuaranceInstance.userId = this.userInfo.id;
                insuaranceInstance.allowAdd = false;
                insuaranceInstance.search(1);
                break;
            // Load component employment-history
            case 5:
                const employmentInstance = this.helperService.createComponent(viewContainerRef, EmploymentHistoryListComponent);
                employmentInstance.userId = this.userInfo.id;
                employmentInstance.allowAdd = false;
                employmentInstance.search(1);
                break;
            // Load component training-history
            case 6:
                const trainingInstance = this.helperService.createComponent(viewContainerRef, TrainingHistoryListComponent);
                trainingInstance.userId = this.userInfo.id;
                trainingInstance.allowAdd = false;
                trainingInstance.search(1);
                break;
            // Load component user-record
            case 7:
                const recordInstance = this.helperService.createComponent(viewContainerRef, RecordsManagementFormComponent);
                recordInstance.userId = this.userInfo.id;
                recordInstance.allowAdd = false;
                recordInstance.getListRecords();
                break;
            // Load component commendation-discipline
            case 8:
                const commendationInstance = this.helperService.createComponent(viewContainerRef, CommendationDisciplineListComponent);
                commendationInstance.userId = this.userInfo.id;
                commendationInstance.allowAdd = false;
                commendationInstance.search(1);
                break;
            case null:
                // this.location.back();
                this.onCloseForm.emit();
                break;
        }
    }

    closeForm() {
        // this.router.navigate(["/user"]);
        // this.location.back();
        this.onCloseForm.emit();
    }
}
